import React, { Component } from 'react';


class Index extends Component {

   render(){
	   return(
    <div className="error-page">
<div className="container">
    <div className="row">
        <div className="col-md-12">
            <div className="error-template">
                <h1>
                    Oops!</h1>
                <h2>
                    {this.props.error?this.props.error.msg:'404 Not Found'}</h2>
                <div className="error-details">
                {this.props.error?this.props.error.errormsg:'Sorry, an error has occured, Requested page not found!'}
                    
                </div>
        
            </div>
        </div>
    </div>
</div>
<style>
    {`
    .error-page { background-image: url(data:image/png;base64,);
        height:500px;
    }
    .error-template {padding: 40px 15px;text-align: center;}
    .error-actions {margin-top:15px;margin-bottom:15px;}
    .error-actions .btn { margin-right:10px; }
    `}
</style>
</div>
	   )
	   }
}

export default Index; 